
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapState, mapMutations, mapGetters } from "vuex";
import VmBackTop from "vue-multiple-back-top";
export default {
name: "app",
components: {
XHeader,
Actionsheet,
VmBackTop,
Badge


},
data: function() {
return {
badgeText:'',
};
},
computed: {
isShowMoreMenus: {
get() {
return this.$store.state.isShowMoreMenus;
},
set(nval) {
this.mutationShowMoreMenus(nval);
}
},
...mapGetters(["moreMenus"]),
...mapState(["isLogined", "unLoginMoreMenus", "loginedMoreMenus"])
},
methods: {
getUnreadMsgCount(){
var app = this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accessToken
}
}).then(function(response){
app.badgeText = response.data.data;
console.log(response.data.data)
})
},
showMoreMenus() {
this.isShowMoreMenus = true;
},
handleClickMoreMenu(menuKey, menuItem) {
this.$router.push("/" + menuKey);
},
...mapMutations([
"mutationShowMoreMenus",
"mutationLogin",
"mutationLoginName",
"mutationAccessToken",
"mutationAuthorId"
])
},
created() {
var loginStatus = window.localStorage.getItem("loginStatus");
var loginName = window.localStorage.getItem("loginName");
var accessToken = window.localStorage.getItem("accessToken");
var authorId = window.localStorage.getItem("authorId");
if (loginStatus) {
var app = this;
this.mutationLogin(loginStatus);
this.mutationLoginName(loginName);
this.mutationAccessToken(accessToken);
this.mutationAuthorId(authorId);
window.setInterval(app.getUnreadMsgCount,60000);
}
}
};



