
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "user",
props: ["loginName"],
components: {
Card,
Group,
Cell
},
data() {
return {
userinfo: "",
collect_topics: ""
};
},
beforeRouteEnter(to, from, next) {
next(function(vm) {
vm.$http.get("/user/" + to.params.loginName).then(function(response) {
console.log(response.data.data);
vm.userinfo = response.data.data;
});
vm.$http
.get("/topic_collect/" + to.params.loginName)
.then(function(response) {

vm.collect_topics = response.data.data.length;
});
});
},
beforeRouteUpdate(to, form, next) {
var app = this;
this.$http.get("/user/" + to.params.loginName).then(function(response) {
app.userinfo = response.data.data;
});
app.$http
.get("/topic_collect/" + to.params.loginName)
.then(function(response) {

app.collect_topics = response.data.data.length;
});
next();
}
};



