import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [
            { name: "all", desc: "全部", selected: true },
            { name: "good", desc: "精华", selected: false },
            { name: "share", desc: "分享", selected: false },
            { name: "ask", desc: "回答", selected: false },
            { name: "job", desc: "招聘", selected: false },
            { name: "dev", desc: "客户端测试", selected: false }
        ],
        unLoginMoreMenus: {
            login: "登录"
        },
        loginedMoreMenus: {
            create:"发表新话题",
            center: "个人中心",
            message: "信息通知",
            logout: "注销",
            
        },
        isLogined: false,
        accessToken:'',
        loginName:'',
        author_id:'',
        isShowMoreMenus: false
    },
    getters: {
        moreMenus(state) {
            if (state.isLogined) {
                return state.loginedMoreMenus;
            }
            return state.unLoginMoreMenus;
        },
        getTopicDescByTab(state){
            return function(tab){
                for(var i=0;i<state.topics.length;i++){
                    if(state.topics[i].name == tab){
                        return state.topics[i].desc;
                    }
                }
            }
        }
    },
    mutations,
    actions,
})

export default store;


