
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import { mapMutations } from "vuex";
export default {
name: "login",
components: {
XInput,
Group,
XButton,
Toast,
Msg
},

data() {
return {
isShowMsg: false,
buttons: [
{
type: "primary",
text: "返回首页",
link: "/"
}
],
accesstoken: "0744d02a-0410-470e-a666-419366b68271",
toastText: "",
isShowToast: false
};
},
created() {
if (this.$store.state.isLogined) {
this.isShowMsg = true;
return;
}
},
methods: {
dologin() {
var app = this;
this.$http
.post("/accesstoken", {
accesstoken: this.accesstoken
})
.then(function(response) {
if (response.data.success) {
window.localStorage.setItem("loginStatus", true);
window.localStorage.setItem("loginName", response.data.loginname);
window.localStorage.setItem("accessToken", app.accesstoken);
window.localStorage.setItem("authorId", response.data.id);
app.$router.push("/user/" + response.data.loginname);

var loginName = window.localStorage.getItem("loginName");
var accessToken = window.localStorage.getItem("accessToken");
var authorId = window.localStorage.getItem("authorId");

app.mutationLogin(true);
app.mutationLoginName(loginName);
app.mutationAccessToken(accessToken);
app.mutationAuthorId(authorId);
}
})
.catch(function(error) {
app.isShowToast = true;
app.toastText = error.response.data.error_msg;
});
},
...mapMutations([
"mutationLogin",
"mutationLoginName",
"mutationAccessToken",
"mutationAuthorId"
])
}
};



