
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from "vuex";
export default {
name: "topic",
props: ["id"],
components: {
CellBox,
Group,
Flexbox,
FlexboxItem,
XButton,
XTextarea,
Popup
},
directives: {
TransferDom
},
computed: {
...mapState(["isLogined"])
},
data() {
return {
topic: null,
isShowPopup: false
};
},
methods: {
showPopup(id) {
this.isShowPopup = true;
},
collect() {
var app = this;
this.$http
.post("/topic_collect/collect", {
accesstoken: this.$store.state.accessToken,
topic_id: this.topic.id
})
.then(function(response) {
if (response.data.success) {
app.topic.is_collect = true;
}
});
},
decollect() {
var app = this;
this.$http
.post("/topic_collect/de_collect", {
accesstoken: this.$store.state.accessToken,
topic_id: this.topic.id
})
.then(function(response) {
if (response.data.success) {
app.topic.is_collect = false;
}
});
},
},
beforeRouteEnter(to, from, next) {
next(function(vm) {
vm.$http
.get("topic/" + to.params.id, {
params: {
accesstoken: vm.$store.state.accessToken
}
})
.then(function(response) {
console.log(response.data.data);
vm.topic = response.data.data;
});
});
},
beforeRouteUpdate(to, from, next) {
var app = this;
this.$http
.get("topic/" + to.params.id, {
params: {
accesstoken: vm.$store.state.accessToken
}
})
.then(function(response) {
app.topic = response.data.data;
});
next();
}
};



