
import Msg from 'vux/src/components/msg/index.vue'

export default {
name: "logout",
components: {
Msg
},
data() {
return {
isShowMsg: false,
buttons: [
{
type: "primary",
text: "跳转到登录页面",
link: "/login"
}
]
};
},
created() {
if (this.$store.state.isLogined == false) {
this.isShowMsg = true;
return;
}
this.$store.commit("mutationLogout");

window.localStorage.clear();

this.$router.push("/");
}
};



